.stream-list-item-container{
    width: 100%;
    height: min-content;
    list-style: none;
}
.stream-list-item{
    font-size: 48px;
    line-height: 59px;
}
.stream-list-item-count{
    font-size: 9px;
    vertical-align: 25px;

    margin-top: 2px;
}


.stream-list-item-container :hover{
    opacity: 0.5;
}






@media screen and (max-width: 1190px){
    .stream-list-item{
        font-size: 40px;
    }
}