
/* 
    @className : .header

    @description
    manges the frame header
*/
.header{
    position: relative;
    margin: 0%;

    font-size: 25px;
    font-weight: 600;
    /* left: 25px;
    top: 15px; */

    margin-left: 25px;
    margin-top: 15px;
}

.header p{
    margin: 0%;
}



/* 
    @className : .main-subtitle

    @description
    manges the frame subtitle
*/
.main-subtitle{
    position: absolute;
    margin:0%;

    left:106px;
    /* top:162px; */
    top:214px;
}

.stream-icon{
    vertical-align: middle;
    margin-left: 2px;
}




/* 
    @className : .stream-container

    @description
    manges the scrollable div
*/
.stream-container{
    display: flex;
    position: absolute;
    left: 101px;
    /* top: 186px; */
    top:238px;
    width: 458px;
    height: 319px;
    overflow-x: hidden;
    overflow-y: scroll;

    scroll-behavior: smooth;
    scrollbar-width: none;
    -ms-overflow-style: none;
}

/* 
    @className : .stream-container-scroll

    @description
    manges the arrow-keys
*/
.stream-container-scroll{
    display: flex;
    height: 319px;
    position: absolute;
    flex-direction: column;
    /* top: 186px; 52px due to 50px nav bar*/
    top:238px;
    left: 555px;
}
.stream-container-scroll-button{
    border: none;
    padding: 0;
    margin: 0;
    text-decoration: none;
    background: none;
}
.stream-container-scroll-button.down{
    border: none;
    padding: 0;
    margin: 0;
    text-decoration: none;
    background: none;

    position: absolute;
    bottom: 0px;
}
.stream-container-scroll-button :hover{
    opacity: 0.5;
}









@media screen and (max-width: 1190px){
 
    .main-subtitle{
        left: 30px;
    }

    .stream-container{
        left: 25px;
        width: 369px;
    }

    .stream-container-scroll{
        left: 390px;
    }

}