.main-holder{
    background: url('../../Res/Images/HomePageDemoImage.svg');
    background-repeat: no-repeat;
    background-size: cover;
    /* background-size: 50vh calc(100vh - 50px); */
}


.add-button{
    border: none;
    padding: 0;
    margin: 0;
    text-decoration: none;
    background: none;

    border-radius: 50%;
    width: min-content;
    position: absolute;
    bottom: 20px;
    right: 25px;
}

.add-button :hover{
    border-width: none;
    border-radius: 50%;
    background: #AFAFAF;
    box-shadow: 0px 0px 8px 3px #AEAEAE;
    
}